	module.exports = {"data":{"markdownRemark":{"html":"<p>Our October 3, 2015 Summit began at noon EST on Saturday on <a href='http://twitch.tv/freecodecamp' target='_blank' rel='nofollow'>twitch.tv channel</a> and be available afterward on our <a href='https://www.youtube.com/channel/UC8butISFwT-Wl7EV0hUK0BQ?sub_confirmation=1' target='_blank' rel='nofollow'>youtube channel</a>.</p>\n<p>During this 1 hour live stream, we’ll:</p>\n<ul>\n<li>launch our free Front End and Full Stack Development Certificate programs</li>\n<li>demo our new, more comprehensive portfolio pages</li>\n<li>demo a new way that all our campers can immediately start helping nonprofits</li>\n<li>talk with camper Blake Johnston, who created the Clementine.js boilerplate that Free Code Camp will use for our full stack Back End Project challenges</li>\n<li>launch our first-ever t-shirt campaign on teespring.com</li>\n</ul>","fields":{"slug":"/meta/free-code-camps-summit-october-2015/"},"frontmatter":{"title":"Free Code Camps Summit October 2015"}}},"pathContext":{"slug":"/meta/free-code-camps-summit-october-2015/"}}

